<template>
  <v-content>
    <v-container fluid fill-height>
      <v-layout align-center justify-center>
        <v-flex xs12 sm8 md4>
          {{user}}
        </v-flex>
      </v-layout>
    </v-container>
  </v-content>
</template>

<script>
  import {mapState} from 'vuex';

  export default {
    name: 'Wall',
    computed: {
      ...mapState({
        user: state => state.user
      })
    }
  };
</script>



