<template>
  <div>
    <v-toolbar
      color="red"
      dense
      fixed
      app
    >
      <v-icon class="mx-3">star</v-icon>
      <v-toolbar-title class="mr-5 align-center">
        <span class="title">Saezu</span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-layout row align-center style="max-width: 650px">
        <v-text-field
          placeholder="Search..."
          single-line
          append-icon="search"
          :append-icon-cb="() => {}"
          color="white"
          hide-details
        ></v-text-field>
        <v-menu
          v-model="userMenu"
          bottom
          offset-y
          nudge-top="-6"
          v-if="user"
        >
          <v-btn icon slot="activator">
            <v-avatar size="36">
              <img src="/static/img/logo.png" alt="avatar">
            </v-avatar>
          </v-btn>
          <v-card>
            <v-list>
              <v-list-tile>
                <v-list-tile-content>
                  <v-list-tile-title>{{user.name}}</v-list-tile-title>
                  <v-list-tile-sub-title>@{{user.username}}</v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
            </v-list>
            <v-divider></v-divider>
            <v-list>
              <v-list-tile v-for="item in userMenuItems" :key="item.title" @click="item.click" :to="item.to">
                <v-list-tile-action v-if="item.icon">
                  <v-icon>{{item.icon}}</v-icon>
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title>{{item.title}}</v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>
            </v-list>
          </v-card>
        </v-menu>
      </v-layout>
    </v-toolbar>
    <v-content>
      <router-view></router-view>
    </v-content>
  </div>
</template>

<script>
  import {mapState} from 'vuex';

  export default {
    name: 'Main',
    data() {
      return {
        userMenu: false,
        userMenuItems: [
          {
            title: 'Profile',
            icon: 'account_box',
            click: () => {
            },
            to: '/user/'
          },
          {
            title: 'Settings',
            icon: 'settings',
            click: () => {
            },
            to: '/settings'
          },
          {
            title: 'Logout',
            icon: 'exit_to_app',
            click: this.logout
          }
        ]
      };
    },
    methods: {
      logout() {
        this.$store.dispatch('logout')
          .then(() => {
            this.$router.push({path: '/login'});
          });
      }
    },
    computed: {
      ...mapState({
        user: state => state.user
      })
    },
    created() {
      for (let item of this.userMenuItems) {
        if (!item.hasOwnProperty('title') || item.title !== 'Profile') {
          continue;
        }
        item.to = `/user/${this.user.username}`;
        break;
      }
    }
  };
</script>

<style scoped>

</style>



