import {serverAPI} from '@/api';

export default {
  async login({commit, dispatch}, payload) {
    const success = await serverAPI.post('/connect', {
      username: payload.username,
      password: payload.password
    })
      .then(response => {
        if (response.data.data && response.data.data.id_token) {
          serverAPI.defaults.headers.common['Authorization'] = `Bearer ${response.data.data.id_token}`;
          return serverAPI.get('/me');
        } else {
          return Promise.reject(new Error('Wrongly formatted response'));
        }
      })
      .then(response => {
        commit('setUser', response.data.data);
        return true;
      })
      .catch(error => {
        console.error(error);
        commit('setError', error.response.data.message);
        return false;
      });
    return success;
  },
  async signUp({commit}, payload) {
    const success = await serverAPI.post('/users', {
      username: payload.username,
      name: payload.name,
      password: payload.password
    })
      .then(response => {
        return true;
      })
      .catch(error => {
        console.error(error);
        commit('setError', error.response.data.message);
        return false;
      });
    return success;
  },
  async logout({commit}) {
    delete serverAPI.defaults.headers.common['Authorization'];
    commit('clearUser');
    return true;
  }
};



